import * as React from 'react';
import { CopyblocklinkNormal } from '@ali/we-icon';
import { IconButton } from '@ali/we-toolbar';
import { IconButtonTitle } from './styled';

interface CopyButtonProps {
  /** 复制块引用链接 */
  onCopy: () => void;
  /** 标题 */
  title?: string;
}

const CopyButton: React.FC<CopyButtonProps> = (props) => {
  const { title, onCopy, children } = props;

  return (
    <IconButton
      testid="refblock-copy"
      tooltip={title}
      icon={
        <>
          <CopyblocklinkNormal />
          {children && <IconButtonTitle>{children}</IconButtonTitle>}
        </>
      }
      onClick={onCopy}
    />
  );
};

export default CopyButton;
